<template>
  <div class="export-key">
    	<div v-if="step==1" style="padding: 20px 10%;">
			<p style="color: #b5b5b5;">请输入交易密码</p>
			<deal-password v-model="dealPassword"></deal-password>
		</div>
		
		<div v-if="step==2">
			<p style="margin:20px;font-size: 12px;color:black;background: #e5e5e5;padding:10px 20px;">注意！拥有钱包私钥可安全控制该地址的资产，不要分享给任何人。</p>
			<div style="margin:0 40px;">
				<p style="font-size: 14px;text-align: center">以下是ETH的地址和私钥，点击可复制</p>
				<p style="margin-top: 25px;margin-bottom: 5px;color: #a5a5a5;">地址</p>
				<mu-text-field readonly v-model="address" style="padding: 10px;border: 1px solid #e5e5e5;" multi-line :solo="true" :rows="4" full-width></mu-text-field><br/>
				<p style="margin-bottom: 5px;color: #a5a5a5;">私钥</p>
				<mu-text-field readonly v-model="privateKey" style="padding: 10px;border: 1px solid #e5e5e5;" multi-line :solo="true" :rows="4" full-width></mu-text-field><br/>
				<!--<mu-button @click="step=3" class="creatBtn" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">下一步</mu-button>-->
			</div>
		</div>	
		
		<div v-if="step==3">
			<div style="margin:40px;text-align: center;">
			<i class="iconfont" style="font-size:100px;color: #295DF1;margin-top: 30px;">&#xe669;</i>
			<p>钱包导出成功!</p>
			<mu-button class="creatBtn" @click="entryWallet" style="width: 100%;margin-top: 40px;height: 44px;" color="#295DF1">进入钱包</mu-button>
		</div>
		</div>	
		</div>
  </div>
</template>
<script>
import DealPassword from '@/components/dealPassword'
export default {
  name: "ExportKey",
  components:{
  	'deal-password':DealPassword
  },
  data() {
    return {
      step:1,
      address:'',
      privateKey:'',
      dealPassword:'',
      walletInfo:{}
    };
  },
  created(){
  	this.walletInfo = JSON.parse(this.$route.query.details);
  	this.address=this.walletInfo.address;
  	this.privateKey=this.walletInfo.privateKey;
  },
  methods: {
		entryWallet(){
			this.$router.replace('/myWallet');
			this.$router.go(-1);
		}
  },
  
  computed: {
    
  },
  watch:{
  	
  	dealPassword(newV,oldV){
  		if(newV.length == 6) {
				if(this.walletInfo.pwd == newV) {
					this.step=2;
				} else {
					this.$layer.open({
						content: '密码错误',
						skin: 'msg',
						time: 2 //2秒后自动关闭
					})
				this.dealPassword=''
				}
			}
  	}
  }
};
</script>
<style scoped>
	
</style>




